            </div>
        </div>
    </div>
    <div class="selectlist" v-else v-for="(item,index) in selectlist" :key="index">
        <div class="listbox" @click="xq(item._id)">
                  <div class="nr" v-if="item.imageSrc.length === 1">
                    <div class="listtitle">{{ item.title }}</div>
                    <div class="listimg">
                      <img :src="item.imageSrc[0]" alt="" />
                    </div>
                  </div>
                  <div class="nrs" v-else-if="item.imageSrc.length > 1">
                    <div class="listtitle">{{ item.title }}</div>
                    <div class="listimgs">
                      <div v-for="(i, index) in item.imageSrc" :key="index">
                        <img :src="i" alt=""  />
                      </div>
                    </div>
                  </div>
                  <div class="nrss" v-else>
                    <div class="listtitle">{{ item.title }}</div>
                  </div>
                  <div class="bigfoot">
                    <div class="listfoot">
                      <div>{{ item.author }}</div>
                      <div>{{ item.comment }}评论</div>
                      <div>{{ new Date(item.time).toLocaleDateString() }}</div>
                    </div>
                  </div>
                </div>
    </div>
            
  </div>
</template>

<script>
import axios from 'axios'
import { Toast,Search,List,Cell  } from 'vant';
import { set } from 'vue';
export default {
  name:'search',
    components:{
        [Search .name]:Search,
        [List .name]:List,
        [Cell .name]:Cell,
    },
    data() {
    return {
      value: '',
      a:true,
      selectlist:[],
      history:[]
    };
  },
  methods: {
    xq(id) {
      // console.log(id);
      this.$router.push({
        name: "content",
        params: {
          id: id,
        },
      });
    },
    onSearch() {
        if(!this.value) return
        this.a = false
        axios({
            method:'post',
            url:' https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search',
            data:{
                "skip": 0, //起始条数
                "limit": 50, //一次返回的条数
                "key_word": this.value //关键字
            }
        }).then(res =>{
            console.log(res.data.data);
            this.selectlist = res.data.data
        })
        // console.log(this.value);
        this.value.length === 0 ? null : this.history.unshift(this.value.trim())
        this.history = [...new Set(this.history)];
        // console.log(this.history);
    },
    clear(){
        this.a = true
    },
    input(){
        if(this.value.length === 0){
            this.a = true
        }
        // console.log(this.value);
    },
    onCancel() {
    //   Toast('取消');
      this.$router.replace('/index')
    // this.a = !this.a
    },
    delhistory(index){
        this.history.splice(index,1)
    },
    delall(){
        this.history.splice(0,this.history.length)
    },
    hsearch(k){
        this.value = k;
        // console.log(k);
        this.onSearch()
    }
  },
  activated(){
    console.log(1);
  }
}
</script>

